import "./OurTeamCard.scss"
import React from "react"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faFacebookF, faTwitter } from '@fortawesome/free-brands-svg-icons'
import { faRss } from '@fortawesome/free-solid-svg-icons'

class OurTeamCard extends React.Component {
  constructor() {
    super()

    this.state = {

    }
  }
  render() {
    return(
      <div className="ourTeamCard">
        <img loading="lazy" className="cardImage img-fluid mx-auto d-block" src={this.props.urlImagen} alt="imagen"/>
        <h3 className="cardSubtitle text-center mt-3">{this.props.subtitulo}</h3>
        <h4 className="cardSubtitle2 text-center text-white">{this.props.subtitulo2}</h4>
        <p className="cardParagraph text-center">{this.props.parrafo}</p>
        <div className="socialMediaBox pb-3">
          <ul className="socialMediaList col-12 d-flex justify-content-center p-0">
            <li className="m-2 p-1 rounded-circle bg-light d-flex justify-content-center align-items-center">
              <FontAwesomeIcon className="socialMedia" icon={faFacebookF} />
            </li>
            <li className="m-2 p-1 rounded-circle bg-light d-flex justify-content-center align-items-center">
              <FontAwesomeIcon className="socialMedia" icon={faTwitter} />
            </li>
            <li className="m-2 p-1 rounded-circle bg-light d-flex justify-content-center align-items-center">
              <FontAwesomeIcon className="socialMedia" icon={faRss} />
            </li>
          </ul>
        </div>       
      </div>
    )
  }
}

export default OurTeamCard